var db = require('./lib/compiledCities.json');

var compCities = {
  getAll: function getAll() {
    return db;
  },
  getCountriesShort: function getCountriesShort() {
    var res = [];

    for (var key in db) {
      res.push(key);
    }

    return res;
  },
  getCountryByShort: function getCountryByShort(shortName) {
    if (typeof db[shortName] !== 'undefined') {
      return db[shortName];
    } else {
      return null;
    }
  },
  getCountryInfoByShort: function getCountryInfoByShort(shortName) {
    if (typeof db[shortName] !== 'undefined') {
      var res = {};

      for (var key in db[shortName]) {
        if (key !== 'states') {
          res[key] = db[shortName][key];
        }
      }

      return res;
    } else {
      return null;
    }
  },
  getStatesByShort: function getStatesByShort(shortName) {
    if (typeof db[shortName] !== 'undefined') {
      var res = [];

      if (typeof db[shortName].states !== 'undefined') {
        res = Object.keys(db[shortName].states);
        return res;
      } else {
        return null;
      }
    } else {
      return null;
    }
  },
  getCities: function getCities(shortName, state) {
    if (typeof db[shortName] !== 'undefined') {
      if (typeof db[shortName].states != 'undefined') {
        var res = [];

        for (var idx in db[shortName].states[state]) {
          res.push(db[shortName].states[state][idx].name);
        }

        return res;
      } else {
        return null;
      }
    } else {
      return null;
    }
  },
  getCountries: function getCountries() {
    var res = [];

    for (var shortName in db) {
      var obj = {};

      for (var key in db[shortName]) {
        if (key !== 'states') {
          obj.shortName = shortName;
          obj[key] = db[shortName][key];
        }
      }

      res.push(obj);
    }

    return res;
  }
};
module.exports = compCities;